import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    data() {
        return {
            // 页面提示
            alertText: 2,
            // tab数据
            navList:[
                {name:'总览',lan_name:'数据总览'},
                {name:'运动',lan_name:'今日运动'},
                {name:'睡眠',lan_name:'昨晚睡眠'},
                {name:'健康',lan_name:'今日健康数据'}
            ],
            // tab数据索引
            nowIndex:0,
            // swiper组件注册对象
            swiperOption:{
                initialSlide:0,
                autoplay:false,
                keyboardControl:true,
                autoHeight:true,
                observer:true,
                observeParents:true,
            }
        }
    },
    computed: {
        swiper() {
            return this.$refs.mySwiper.swiper
        }
    },
    components:{
        swiper,
        swiperSlide
    },
    mounted() {
        
    },
    methods: {
        // tab按钮点击事件
        tabClick(index){
            this.nowIndex = index
            this.swiper.slideTo(index,1000,false)
        },
        // 监听滑动事件
        touchEndFun(event){
            console.log(this.swiper.activeIndex)
            this.nowIndex = this.swiper.activeIndex
        },
        // 导航右标题跳转事件
        rightClickLink(bool){
            if(bool == 'info'){
                this.$router.push('/views/healthData/perinfo')
            }
            if(bool == 'history'){

            }
        }
    }
}



